import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Camera, CameraOptions } from '@ionic-native/camera';
/**
 * Generated class for the UserUpdatePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-user-update',
  templateUrl: 'user-update.html',
})
export class UserUpdatePage {

  name: string;
  phone: any;
  email: any;
  imgSrc: string;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public camera: Camera,) {
      var userData = this.navParams.data;
      this.name = userData.name;
      this.phone = userData.phone;
      this.email = userData.email;
      this.imgSrc = userData.img;
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad UserUpdatePage');
  }

  takeImage(){
    const options: CameraOptions = {
      quality: 65,
      sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
      destinationType: this.camera.DestinationType.DATA_URL,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: this.camera.MediaType.PICTURE
    }
    this.camera.getPicture(options).then((imageData) => {
      // imageData is either a base64 encoded string or a file URI
      // If it's base64:
      let base64Image = 'data:image/jpeg;base64,' + imageData;
      this.imgSrc = base64Image;
      
    }, (err) => {
      // Handle error
    });
  }

}



